import UserServices from "@/services/UserServices.js";
import apiClient from "@/services/apiClient.js";
import updateLocalStorage from "@/utils/updateLocalStorage";
import { domainName } from "@/services/apiClient";

const user = {
    namespaced: true,
    state: {
        user: {
            email: "",
            name: "",
            phone: "",
            logo: "",
            avatar: "",
            primaryColor: "",
            secondaryColor: "",
            isAdmin: null,
            token: null,
        },
        users: [],
        targetUsers: [],
    },
    mutations: {
        SET_USER(
            state, {
                email,
                name,
                phone,
                avatar,
                logo,
                primaryColor,
                secondaryColor,
                isAdmin,
                token,
            }
        ) {
            state.user = {
                email: email,
                name: name,
                phone: phone,
                logo: `${domainName}images/logos/${logo}`,
                avatar: `${domainName}images/avatars/${avatar}`,
                primaryColor: primaryColor,
                secondaryColor: secondaryColor,
                isAdmin: isAdmin,
                token: token,
            };
            localStorage.setItem(
                "user",
                JSON.stringify({
                    email,
                    isAdmin,
                    phone,
                    avatar,
                    logo,
                    name,
                    primaryColor,
                    secondaryColor,
                    token,
                })
            );
            apiClient.defaults.headers.common["Authorization"] = `Bearer ${token}`;
        },
        LOGOUT() {
            localStorage.removeItem("user");
            location.reload();
        },
        SET_USERS(state, users) {
            state.users = users;
        },
        SET_TARGET_USERS(state, targetUsersIds) {
            state.targetUsers = targetUsersIds;
        },
        CLEAR_TARGET_USERS(state) {
            state.targetUsers = [];
        },
        UPDATE_LOGO(state, url) {
            state.user.logo = url;
            updateLocalStorage({
                logo: url.replace(`${domainName}images/`, ""),
            });
        },
        UPDATE_AVATAR(state, url) {
            state.user.avatar = url;
            updateLocalStorage({
                avatar: url.replace(`${domainName}images/`, ""),
            });
        },
        UPDATE_COLORS(state, { primary, secondary }) {
            state.user.primaryColor = primary;
            state.user.secondaryColor = secondary;
            updateLocalStorage({
                primaryColor: primary,
                secondaryColor: secondary,
            });
        },
    },
    actions: {
        fetchUsers({ commit, dispatch }) {
            UserServices.getUsers()
                .then((response) => {
                    commit("SET_USERS", response.data.data);
                })
                .catch((error) => {
                    let notification = {
                        type: error,
                        message: `there was an error while fetching users : ${error}`,
                    };
                    dispatch("notification/add", notification, { root: true });
                });
        },
        // eslint-disable-next-line no-unused-vars
        setTargetUsers({ commit, state }, targetUsers) {
            let targetUsersIds = [];
            for (var targetUser of targetUsers) {
                state.users.forEach((user) => {
                    if (user.email === targetUser) {
                        targetUsersIds.push(user.id);
                    }
                });
            }
            commit("SET_TARGET_USERS", targetUsersIds);
        },
        clearTargetUsers({ commit }) {
            commit("CLEAR_TARGET_USERS");
        },
        updateLogo({ commit }, url) {
            commit("UPDATE_LOGO", url);
        },
        updateAvatar({ commit }, url) {
            commit("UPDATE_AVATAR", url);
        },
        updateColors({ commit }, colors) {
            commit("UPDATE_COLORS", colors);
        },
        login({ commit, dispatch }, { isAdminLogin, email, password }) {
            if (isAdminLogin) {
                return UserServices.adminLogin({ email: email, password: password })
                    .then((response) => {
                        commit("SET_USER", response.data);
                        let notification = {
                            type: "success",
                            message: "you are logged in",
                        };
                        dispatch("notification/add", notification, { root: true });
                        return response.status;
                    })
                    .catch((error) => {
                        let notification = {
                            type: "error",
                            message: `Verify credentials: ${error}`,
                        };
                        dispatch("notification/add", notification, { root: true });
                    });
            } else {
                return UserServices.userLogin({ email: email, password: password })
                    .then((response) => {
                        commit("SET_USER", response.data);
                        let notification = {
                            type: "success",
                            message: "you are logged in",
                        };
                        dispatch("notification/add", notification, { root: true });
                        return response.status;
                    })
                    .catch((error) => {
                        let notification = {
                            type: error,
                            message: `${error}`,
                        };
                        dispatch("notification/add", notification, { root: true });
                    });
            }
        },
        logout({ commit }) {
            commit("LOGOUT");
        },
    },
    getters: {
        usersEmails: (state) => {
            return state.users.map((user) => user.email);
        },
    },
};
export default user;