import Vue from "vue";
import VueRouter from "vue-router";
import NotFound from "@/views/NotFound.vue";

Vue.use(VueRouter);

const routes = [{
        path: "/",
        name: "home",
        component: () =>
            import ("@/views/Welcome")
    },
    {
        path: "/login",
        name: "login",
        component: () =>
            import ("@/views/Login")
    },
    {
        path: "/templates/:id?",
        name: "templates",
        component: () =>
            import ("@/views/Home"),
        beforeEnter(to, from, next) {
            //to-refactor: into global guard
            let isAuthenticated = localStorage.getItem("user");
            if (!isAuthenticated) {
                next({ name: "login" });
            }
            next();
        }
    },
    {
        path: "*",
        component: NotFound
    }
];

const router = new VueRouter({
    mode: "history",
    base: process.env.BASE_URL,
    routes
});

export default router;