import apiClient from "./apiClient";

export default {
    getTemplates(isAdmin) {
        return apiClient.get(isAdmin ? "admin/templates" : "templates");
    },
    getTemplate(id, isAdmin) {
        return apiClient.get(`${isAdmin ? "admin/templates" : "templates"}/${id}`);
    },
    generatePdf(
        filename,
        htmlContent,
        email,
        logo,
        avatar,
        phone,
        name,
        primaryColor,
        secondaryColor,
        isAdmin
    ) {
        let data = JSON.stringify({
            filename,
            htmlContent,
            email,
            logo,
            avatar,
            phone,
            name,
            primaryColor,
            secondaryColor,
        });
        console.log(data);
        return apiClient.post(isAdmin ? "admin/pdf" : "pdf", data, {
            responseType: "blob",
        });
    },
    uploadImage(file, isAdmin, uploadType) {
        const formData = new FormData();
        if (uploadType) {
            formData.append(uploadType.toLowerCase(), file);
        } else {
            formData.append("image", file);
        }
        return apiClient.post(
            isAdmin ? "admin/upload-image" : "upload-image",
            formData, { "Content-Type": "multipart/form-data" }
        );
    },
    postTemplate(templateDetails) {
        let data = JSON.stringify({
            users_id: Array.from(templateDetails.users_id),
            filename: templateDetails.filename,
            payload: JSON.stringify(templateDetails.payload),
        });
        return apiClient.post("admin/templates", data);
    },
    deleteTemplate(templateId) {
        return apiClient.delete(`admin/templates/${templateId}`);
    },
    editTemplate(templateDetails, isAdmin) {
        let data = JSON.stringify({
            filename: templateDetails.filename,
            payload: templateDetails.payload,
        });
        return apiClient.patch(
            `${isAdmin ? "admin/templates" : "templates"}/${templateDetails.id}`,
            data
        );
    },
};