/* eslint-disable prettier/prettier */
import EditorServices from "../../services/EditorServices";
import EditorJS from "@editorjs/editorjs";
import Header from "@editorjs/header";
import LinkTool from "@editorjs/link";
import RawTool from "@editorjs/raw";
import TableTool from "@editorjs/table";
import SimpleImage from "@editorjs/simple-image";
import ImageTool from "@editorjs/image";
import Checklist from "@editorjs/checklist";
import List from "@editorjs/list";
import Embed from "@editorjs/embed";
import Quote from "@editorjs/quote";
import Marker from "@editorjs/marker";
import Delimiter from "@editorjs/delimiter";
import convertToHtml from "@/utils/helper";
import store from "@/store";
import { domainName } from "@/services/apiClient";

const editor = {
    namespaced: true,
    state: {
        loading: false,
        isEditorHome: true,
        templateName: "",
        isNewTemplate: false,
        isEditingTemplate: false,
        isProfile: false,
        templates: [],
        template: {},
        editor: null,
        editorTools: {
            holder: "editor-holder",
            autofocus: true,
            tools: {
                header: Header,
                list: List,
                linktool: LinkTool,
                rawtool: RawTool,
                simpleimage: SimpleImage,
                delimiter: Delimiter,
                table: {
                    class: TableTool,
                },
                image: {
                    class: ImageTool,
                    config: {
                        uploader: {
                            // eslint-disable-next-line no-unused-vars
                            uploadByFile(file) {
                                return store.dispatch("editor/uploadImage", {
                                    file: file,
                                    isLogo: false,
                                });
                            },
                        },
                    },
                },
                checklist: Checklist,
                embed: Embed,
                quote: Quote,
                marker: Marker,
            },
        },
        editorData: {
            data: {},
        },
    },

    mutations: {
        INIT_EDITOR(state) {
            state.isEditorHome = false;
            //to be changed to isCreatingNewTemplate cause it makes more sense
            state.isNewTemplate = false;
            state.editor = new EditorJS({
                ...state.editorTools,
                ...state.editorData,
            });
        },
        SET_TEMPLATE_NAME(state, templateName) {
            state.templateName = templateName;
        },
        LOAD_TEMPLATES(state, templates) {
            state.templates = templates.data;
        },
        LOAD_TEMPLATE(state, template) {
            state.isEditorHome = false;
            state.isNewTemplate = false;
            state.isProfile = false;
            state.isEditingTemplate = true;
            state.template = template.data;
            state.editorData.data = JSON.parse(state.template.payload);
        },
        LOAD_PROFILE(state) {
            state.isProfile = true;
            state.isEditorHome = false;
            state.isNewTemplate = false;
        },
        CREATE_NEW_TEMPLATE(state) {
            state.isEditorHome = false;
            state.isNewTemplate = true;
            state.isProfile = false;
        },
        DESTROY_EDITOR(state) {
            state.editor = null;
            state.editorData.data = null;
            state.isEditorHome = true;
        },
        CLEAR_TEMPLATE_NAME(state) {
            state.templateName = "";
        },
    },

    actions: {
        initEditor({ commit, state }) {
            if (!state.isEditorHome) {
                commit("INIT_EDITOR");
            }
            return;
        },
        // eslint-disable-next-line no-unused-vars
        saveEditedTemplate({ commit, dispatch, state, rootState }) {
            state.editor.save().then((outputData) => {
                return (
                    EditorServices.editTemplate({
                        payload: outputData,
                        id: state.template.id,
                        filename: state.template.filename,
                    },
                        rootState.user.user.isAdmin
                    )
                        // eslint-disable-next-line no-unused-vars
                        .then((response) => {
                            const notification = {
                                type: "success",
                                message: "template saved successfuly!",
                            };
                            dispatch("notification/add", notification, { root: true });
                            commit("DESTROY_EDITOR");
                        })
                        .catch((error) => {
                            const notification = {
                                type: "error",
                                message: `there was an error while saving template! ${error}`,
                            };
                            dispatch("notification/add", notification, { root: true });
                            commit("DESTROY_EDITOR");
                        })
                );
            });
        },
        saveNewTemplate({ commit, dispatch, state }, targetUsers) {
            state.editor.save().then((outputData) => {
                return (
                    EditorServices.postTemplate({
                        payload: outputData,
                        users_id: targetUsers,
                        filename: state.templateName,
                    })
                        // eslint-disable-next-line no-unused-vars
                        .then((response) => {
                            const notification = {
                                type: "success",
                                message: "template created successfuly!",
                            };
                            dispatch("notification/add", notification, { root: true });
                            dispatch("user/clearTargetUsers", null, { root: true });
                            dispatch("fetchTemplates");
                            commit("CLEAR_TEMPLATE_NAME");
                            commit("DESTROY_EDITOR");
                        })
                        .catch((error) => {
                            const notification = {
                                type: "error",
                                message: `there was an error while creating template! ${error}`,
                            };
                            dispatch("notification/add", notification, { root: true });
                            commit("DESTROY_EDITOR");
                        })
                );
            });
        },
        loadProfile({ commit }) {
            commit("LOAD_PROFILE");
        },
        uploadImage({ rootState }, { file, uploadType }) {
            return (
                EditorServices.uploadImage(
                    file,
                    rootState.user.user.isAdmin,
                    uploadType
                )
                    .then((response) => {
                        if (uploadType) {
                            store.dispatch(
                                `user/update${uploadType}`,
                                response.data.file.url
                            );
                        }
                        const notification = {
                            type: "success",
                            message: "Image uploaded successfully",
                        };
                        store.dispatch("notification/add", notification);

                        return {
                            success: 1,
                            file: {
                                url: response.data.file.url,
                            },
                        };
                    })
                    // eslint-disable-next-line no-unused-vars
                    .catch((error) => {
                        const notification = {
                            type: "error",
                            message: "There was a problem while uploading the image",
                        };
                        store.dispatch("notification/add", notification);
                    })
            );
        },
        createNewTemplate({ commit }) {
            commit("CREATE_NEW_TEMPLATE");
        },
        setTemplateName({ commit }, templateName) {
            commit("SET_TEMPLATE_NAME", templateName);
        },
        fetchTemplates({ commit, dispatch, rootState }) {
            return EditorServices.getTemplates(rootState.user.user.isAdmin)
                .then((response) => {
                    let notification;
                    if (response.data.data.length !== 0) {
                        commit("LOAD_TEMPLATES", response.data);
                        notification = {
                            type: "success",
                            message: "templates fetched successfuly!",
                        };
                    } else {
                        notification = {
                            type: "success",
                            message: "The template table is empty!",
                        };
                    }
                    dispatch("notification/add", notification, { root: true });
                })
                .catch((error) => {
                    const notification = {
                        type: "error",
                        message: `there was an error while fetching templates! ${error}`,
                    };
                    dispatch("notification/add", notification, { root: true });
                });
        },
        destroyEditor({ commit }) {
            commit("DESTROY_EDITOR");
        },
        // eslint-disable-next-line no-unused-vars
        fetchTemplate({ commit, dispatch, state, rootState }, template) {
            //to-refactor
            commit("DESTROY_EDITOR");
            return EditorServices.getTemplate(
                template.id,
                rootState.user.user.isAdmin
            )
                .then((response) => {
                    if (!template.isDownloading) {
                        commit("LOAD_TEMPLATE", response.data);
                        commit("INIT_EDITOR");
                    }
                    const notification = {
                        type: "success",
                        message: "template fetched successfuly!",
                    };
                    dispatch("notification/add", notification, { root: true });
                    return response.data;
                })
                .catch((error) => {
                    const notification = {
                        type: "error",
                        message: `there was an error while fetching template! ${error}`,
                    };
                    dispatch("notification/add", notification, { root: true });
                });
        },
        // eslint-disable-next-line no-unused-vars
        downloadTemplate({ commit, dispatch, state, rootState }, template) {
            let generatedHtml = convertToHtml(
                template.data.payload,
                rootState.user.user.secondaryColor
            );
            EditorServices.generatePdf(
                template.data.filename,
                generatedHtml,
                rootState.user.user.email,
                rootState.user.user.logo?.replace(`${domainName}images/logos/`, "") ??
                "random_url",
                rootState.user.user.avatar?.replace(
                    `${domainName}images/avatars/`,
                    ""
                ) ?? "random_url",
                rootState.user.user.phone ?? "random_phone",
                rootState.user.user.name,
                rootState.user.user.primaryColor ?? "random_color",
                rootState.user.user.isAdmin
            )
                .then((response) => {
                    const url = window.URL.createObjectURL(new Blob([response.data]));
                    const link = document.createElement("a");
                    link.href = url;
                    link.setAttribute("download", template.data.filename + ".pdf");
                    document.body.appendChild(link);
                    link.click();
                    const notification = {
                        type: "success",
                        message: `PDF generated successfuly`,
                    };
                    dispatch("notification/add", notification, { root: true });
                })
                .catch((error) => {
                    const notification = {
                        type: "error",
                        message: `there was a problem while converting to PDF!${error}`,
                    };
                    dispatch("notification/add", notification, { root: true });
                });
        },
        deleteTemplate({ dispatch }, templateId) {
            return EditorServices.deleteTemplate(templateId)
                .then(() => {
                    let notification = {
                        type: "success",
                        message: "Template deleted successfuly",
                    };
                    dispatch("notification/add", notification, { root: true });
                    location.reload();
                })
                .catch((error) => {
                    let notification = {
                        type: "error",
                        message: `there was a problem while deleting template: ${error}`,
                    };
                    dispatch("notification/add", notification, { root: true });
                });
        },
    },

    getters: {
        headers(state) {
            if (!state.loading && state.templates.length !== 0) {
                return [
                    ...Object.keys(state.templates[0]).map((key) => {
                        if (key === "payload") {
                            return false;
                        }
                        return {
                            text: key,
                            value: key,
                        };
                    }),
                    { text: "action", value: "actions" },
                ];
            }
        },
        items(state) {
            return [{
                icon: "mdi-chevron-down",
                "icon-alt": "mdi-chevron-right",
                text: "Templates",
                model: true,
                children: state.templates.map((template) => {
                    return { text: template.filename, id: template.id };
                }),
            },];
        },
        tableItems(state) {
            return state.templates.map((template) => {
                return {
                    id: template.id,
                    filename: template.filename,
                    user_id: template.user_id,
                    created_at: template.created_at,
                    updated_at: template.updated_at,
                };
            });
        },
    },
};
export default editor;