function jsonToHtmlConverter(jsonTemplate, secondaryColor) {
    let html = "";
    jsonTemplate.blocks.forEach(function(block) {
        //this variable is used in the table transformation to know where to set the table's headers
        let isHeaderRow = true;
        switch (block.type) {
            case "header":
                html += `<h${block.data.level}>${block.data.text}</h${block.data.level}>`;
                break;
            case "paragraph":
                html += `<p>${block.data.text}</p>`;
                break;
            case "delimiter":
                html += `<hr style="border-color: ${secondaryColor}"/>`;
                break;
            case "image":
                html += `<img src="${block.data.file.url}" style="display: block;
                margin-left: auto;
                margin-right: auto;
                width: 100%;" title="${block.data.caption}" /><br /><em>${block.data.caption}</em>`;
                break;
            case "list":
                if (block.data.style === "unordered") {
                    html += "<ul>";
                    block.data.items.forEach(function(li) {
                        html += `<li>${li}</li>`;
                    });
                    html += "</ul>";
                } else {
                    html += "<ol>";
                    block.data.items.forEach(function(li) {
                        html += `<li>${li}</li>`;
                    });
                    html += "</ol>";
                }
                break;
            case "table":
                html +=
                    '<table style="width: 100%; border: 1px solid black; border-collapse: collapse;">';
                block.data.content.forEach(function(row) {
                    html += "<tr>";
                    if (isHeaderRow) {
                        row.forEach(function(item) {
                            html += `<th style="border: 1px solid black; border-collapse: collapse;">${item}</th>`;
                        });
                        isHeaderRow = false;
                        return;
                    }
                    row.forEach(function(item) {
                        html += `<td style="border: 1px solid black; border-collapse: collapse;">${item}</td>`;
                    });
                    html += "</tr>";
                });
                html += "</table>";
                break;
            case "quote":
                console.log(block.data.alignment);
                html += `<caption>${block.data.caption}</caption>`;
                html += `<p style="text-align: ${block.data.alignment};">" ${block.data.text} "</p>`;
                break;
            default:
                console.log("Unknown block type", block.type);
                console.log(block);
                break;
        }
    });
    return html;
}

export default jsonToHtmlConverter;