<template>
  <div class="ma-2">
    <v-snackbar :left="left" v-model="snackbar" class="notification-bar">
      {{ notification.message }}
      <v-btn :color="color" text @click="snackbar = false">
        Close
      </v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  props: {
    notification: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      color: this.notification.type === "success" ? "green" : "red",
      timeout: null,
      snackbar: true,
      left: true
    };
  },
  mounted() {
    this.timeout = setTimeout(() => this.remove(this.notification), 5000);
  },
  beforeDestroy() {
    clearTimeout(this.timeout);
  },
  computed: {
    notificationType() {
      return `${this.notification.type}`;
    }
  },
  methods: mapActions("notification", ["remove"])
};
</script>

<style scoped>
.notification-bar {
  margin-bottom: 2%;
}
</style>
